import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import Bmob from '@/utils/bmob.js';

let that;
let search_text;
let uid;
export default {
	components: {
		faIcon,
		loading
	},
	data() {
		return {
			loading: true,
			shops: null,
			is_choose: false
		}
	},

	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');
		
		if (options.type == "choose") {
			that.is_choose = true
		}
	},
	onShow() {
		uni.removeStorageSync("shop")
		that.getshop_list()
	},
	onUnload() {
		search_text =""
	},
	methods: {

		//选择此门店
		select_this(shop) {
			uni.setStorageSync("shop", shop)
			uni.navigateBack({
				delta: 1
			})
		},

		//编辑操作
		edit(item) {
			uni.setStorageSync("shop", item);
			uni.navigateTo({
				url: "add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此门店',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("shops");
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				that.getshop_list()
			}).catch(err => {
				console.log(err)
			})
		},

















		//得到门店列表
		getshop_list: function() {
			that.loading = true;
			const query = Bmob.Query("shops");
			query.order("num");
			query.equalTo("parent", "==", uid);
			if (search_text) {
				query.equalTo("name", "==", {
					"$regex": "" + search_text + ".*"
				});

			}
			query.find().then(res => {
				console.log(res,uid)
				that.loading = false;
				that.shops = res;
			});
		},

	}
}
